.block-partners {
  /*margin-left: -2000px !important;
    margin-right: -2000px !important;
    padding-left: 2000px !important;
    padding-right: 2000px !important;*/
  overflow: hidden;
}

.block-partners .item {
  flex: 0 0 auto;
  width: 293px;
  padding-right: 2px;
}

.block-partners .item__link {
  box-shadow: none !important;
}

.block-partners .arrows {
  z-index: 4;
}

.block-partners .arrows__prev {
  left: -50px;
}

.block-partners .arrows__next {
  right: 0px;
}

.block-partners .b__shadow {
  position: absolute;
  z-index: 4;
  top: 0;
  bottom: 0;
  left: 0;
  width: 292px;
  transition: opacity 0.25s ease-in-out, visibility 0.25s ease-in-out;
  box-shadow: 0 6px 25px 0 rgba(0, 0, 0, 0.2);
  opacity: 0;
  visibility: hidden;
}

.block-partners .b__shadow.active {
  opacity: 1;
  visibility: visible;
}

.block-partners .block__content {
  display: flex;
  flex-flow: row wrap;
}

.block-partners .block__items {
  z-index: 3;
  overflow: hidden;
}

.block-partners .block__items-inner {
  display: flex;
}

.block-partners .b__ft {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.block-partners .b__ft .btn {
  display: none;
  margin-top: 30px;
}

.block-partners .b__dots {
  display: block !important;
  white-space: nowrap;
  overflow: hidden;
}

.block-partners .b__dots .dots__dot {
  display: inline-block;
  position: relative;
  transition: 0.2s transform, 0.2s left;
  vertical-align: top;
}

@media only screen and (max-width: 1199px) {
  .block-partners .item {
    width: 324px;
  }
}

@media only screen and (max-width: 1023px) {
  .block-partners .container {
    padding-right: 0;
    margin-left: calc((100% - 770px) / 2);
    max-width: 100%;
  }
}

@media only screen and (max-width: 1019px) {
  .block-partners .item {
    width: 308px;
  }
}

@media only screen and (max-width: 991px) {
  .block-partners .arrows {
    display: none;
  }

  .block-partners .b__shadow {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .block-partners .b__hd .btn {
    display: none;
  }

  .block-partners .block__content {
    margin-left: -15px;
    margin-right: -15px;
  }

  .block-partners .block__items {
    padding-right: 70px;
  }

  .block-partners .b__ft .btn {
    display: block;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1019px) {
  .block-partners .block__content {
    margin-left: 25px;
    margin-right: 25px;
  }
}